.modal {
  position: relative;
  .close {
    cursor: pointer;
    position: absolute;
    font-size: rem(36px);
    line-height: rem(20px);
    right: 15px;
    top: 15px;
    padding: 0;
    color: #cdcdcd;
    display: inline-block;
    &:hover {
      color: $grey;
    }
  }
  &-header {
    display: block;
    .modal-description{
      font-size: rem(14px);
      width: 225px;
      margin: auto;
    }
  }
  &-title {
    margin-bottom: rem(20px);
  }
  &-body {
    padding: rem(15px 100px 67px 100px);
    .form-control {
      width: 100%;
    }
    .modal-description{
      font-size: rem(14px);
      width: 225px;
      margin: rem( 0 auto 70px);
    }
    .btn {
      width: 100%;
      text-align: center;
      margin-top: rem(10px);
    }
  }
  &-img {
    text-align: center;
    margin-bottom: rem(45px);
  }
}

// --------------custom bootstrap ------------

.modal-header {
  flex-wrap: wrap;
}